<template>
  <div class="about">
    <h1>Профил корсника {{name}}</h1><br/><br/><br/>
    Напишите нови тњит:<br/>
    <input type="textarea" id="tnjit" max="200" size="80" v-model="sadrzaj"/><br/>
    <input type="button" value="Објави" v-on:click="dodaj()" /><br/><br/><br/>
    Повест тњитова<br/>
    <div class="spisak">
    <div class="ploce" v-for="p in podaci"><h5>{{p.korisnickoime}}</h5> <br/>
      &nbsp&nbsp&nbsp&nbsp {{p.sadrzaj}}
      <br/><br/>
      ⬆️{{p.gore}} ⬇️{{p.dole}}&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp
      {{p.datum.slice(p.datum.indexOf('T')+1,p.datum.indexOf('.'))}} {{p.datum.slice(0,p.datum.indexOf('T'))}}
      <br/>
      <input type="button" value="Обриши тњит" v-on:click="obrisi(p.datum,p.sadrzaj)"/>
      </div>

  </div>
  </div>
</template>

<script>
export default {

  props: {
    msg: String,


  },
  data(){
  return{
    name:"",
    prijavljen:false,
    podaci:[],
    sadrzaj:''
    }
  },

  created(){
  const ime=localStorage.getItem('ime');

  if(ime){
     fetch('https://vue-rest.herokuapp.com/get?ime='+ime, {
        method: 'GET',
        headers: { 'Content-Type': 'application/json' ,'Access-Control-Allow-Origin': '*'},
        credentials: 'include',
      })
      .then(obj=>obj.json() )
        .then(res=>{
            this.podaci=res;
            this.name=ime;
          })
        }
  },
  methods:{
    dodaj(){
      let id=localStorage.getItem('id');
      let token=localStorage.getItem('token');

      if(this.sadrzaj){
      fetch('https://vue-rest.herokuapp.com/get', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' ,'Access-Control-Allow-Origin': '*'},
        credentials: 'include',
        body: JSON.stringify({Id:id,Sadrzaj:this.sadrzaj,Token:token})
      }).then(data=>data.json())
      .then(resp=>{ 
        if(resp.status!=400 || resp.status!=500){
          this.$socket.emit('comment',{sadrzaj:resp.sadrzaj,korisnickoime:this.name,datum:resp.datum,doleglasovi:0,goreglasovi:0});
          this.$router.go();
        }
        else{
          alert("Greska u unosa podataka ili upita");}
        });
      }
      else{
        alert('Niste nista uneli');
      }
    },
    obrisi(datum,sadrzaj){
      let id=localStorage.getItem('id');
      let token=localStorage.getItem('token');

      const data={id:id,token:token,datum:datum,sadrzaj:sadrzaj};

      fetch('https://vue-rest.herokuapp.com/get', {
        method: 'DELETE',
        headers: { 'Content-Type': 'application/json' ,'Access-Control-Allow-Origin': '*'},
        credentials: 'include',
        body: JSON.stringify(data)
      }).then(data=>data.json())
      .then(resp=>{ 
        if(resp.status!=400 || resp.status!=500){
          this.$router.go();
        }
        else{
          alert("Greska prilikom brisanja");}
        });
    }
  }
}
</script>